import {iface_state} from './actions_iface';

let attached;

/**
 *    redux   
 */
export default function ifaceMiddleware() {
  return (store) => {
    const {dispatch} = store;
    return next => action => {
      if(!attached) {
        attached = true;

        // TODO:     online-offline, rotate  ..
        if(!attached) {
          dispatch(iface_state(''));
        }
      }
      return next(action);
    };
  };
}

