export const IFACE_STATE = 'IFACE_STATE';         //   

/**
 *
 * @param state {Object}
 * @param state.component {String} -  ,      ( ,   )
 * @param state.name {String} -     
 * @param state.value {String} - 
 * @return {{type: string, payload: *}}
 */
export function iface_state(state) {
  return {
    type: IFACE_STATE,
    payload: state,
  };
}

export default {
  [IFACE_STATE]: iface_state,
};
