// Action types -   

export const META_LOADED = 'META_LOADED'; //       
export const PRM_CHANGE = 'PRM_CHANGE';   //     (couch_path, zone  ..)
export const OFFLINE = 'OFFLINE';         //   
export const IDLE = 'IDLE';               //    (    )
export const SECOND_INSTANCE = 'SECOND_INSTANCE'; //         

// Actions -  -  .     redux

export function meta_loaded({version}) {
  return {
    type: META_LOADED,
    payload: version,
  };
}

export function prm_change(name, value) {
  return {
    type: PRM_CHANGE,
    payload: {name, value},
  };
}

export function offline(state) {
  return {
    type: OFFLINE,
    payload: state,
  };
}

export function idle(state) {
  if(typeof $p === 'object' && $p.ui) {
    $p.ui.idle = state;
  }
  return {
    type: IDLE,
    payload: state,
  };
}

export function second_instance() {
  return {
    type: SECOND_INSTANCE,
    payload: true,
  };
}
